@import url('https://fonts.googleapis.com/css?family=PT+Serif');

a {
  color: black;
}

.App {
  width: 800px;
  margin: auto;
  padding: 30px 10px;
}

.image {
  width: 100%;
  margin: 10px auto;
}

.image img {
  width: 100%;
  border-radius: 10px;
}

header {
  padding: 30px 0;
}

header .icon {
  float: left;
  font-size: 40px;
  margin-top: 10px;
  margin-right: 20px;
}

h1 {
  margin: 0;
}

h2 {
  margin: 0;
}

.step {
  /*opacity: 0.6;*/
  border-top: 1px solid #eee;
  position: relative;
  margin-top: 30px;
  margin-bottom: 70px;
  opacity: 1;
}

.step.current {
  
}

.step .number {
  position: absolute;
  top: -15px;
  left: -26px;
  font-weight: bold;
  font-size: 14px;
  border: 1px solid #eee;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  background: #ffffff;
  text-align: center;
  line-height: 25px;
}

.step .more {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px;
  font-weight: bold;
  background-color: #E3D0FF;
  border-radius: 10px;
  font-size: 14px;
  border: 2px solid #9C6ADE;
  line-height: 20px;
}

.step .more strong {
  line-height: initial;
  font-size: 10px;
  margin-right: 10px;
  border: 1px solid;
  padding: 3px 8px;
  border-radius: 10px;
  background-color: #E3D0FF;
  color: #9C6ADE;
  border-color: #9C6ADE;
  font-weight: bold;
  min-width: 58px;
  text-align: center;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

video {
  width: 100%;
  max-height: 450px;
  margin: 30px 0;
  margin-bottom: 40px;
  border-radius: 10px;
}

p, ul {
  font-family: 'PT Serif', serif;
  font-size: 24px;
  line-height: 34px;
}

em {
  color: #333;
  font-style: italic;
}

ul {
  margin: 0;
}

ul li {
  margin-bottom: 20px;
}

pre {
  margin: 20px 0;
}

.hljs {
  border-radius: 6px;
  padding: 15px 10px;
  background-color: rgba(0, 0, 0, 0.02);
  font-size: 18px;
  line-height: 20px;
}



